.App {
  text-align: center;
  font-family: Roboto, sans-serif;
}

.breed-selector {
  margin: 20px;
}

.breed-selector input[type='text'] {
  padding: 8px;
  width: 200px;
  margin-bottom: 10px;
}



.breed-selector ul {
  list-style: none;
  padding: 0;
}

.breed-selector li {
  margin: 5px 0;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
}

.gallery-item {
  position: relative;
}
.gallery-item .overlay {
  position: absolute;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  width: 100%;
  text-align: center;
  padding: 5px;
  opacity: 0;
  transition: opacity 0.3s ease;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.gallery-item:hover .overlay {
  opacity: 1;
}

.gallery img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  margin: 5px;
  border-radius: 10px;
}

.pagination {
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pagination button {
  padding: 10px;
  margin: 0 10px;
}


.sadsnoopy {
  text-align: center;
  margin-top: 50px;
}

.sadsnoopy img {
  max-width: 300px;
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.sadsnoopy p {
  font-size: 1.5em;
  color: #555;
}

/* Quiz styles */
.quiz {
  margin: 20px auto;
  max-width: 600px;
  text-align: center;
}

.quiz h2 {
  font-size: 1.5em;
  margin-bottom: 20px;
}

.quiz-options {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.quiz-option-button {
  background-color: #4caf50;
  color: white;
  padding: 12px 24px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 80%;
  font-size: 1em;
  border-radius: 4px;
}

.quiz-option-button:hover {
  background-color: #45a049;
}

.quiz-result {
  text-align: center;
  margin-top: 40px;
}

.quiz-result h2 {
  font-size: 2em;
  margin-bottom: 20px;
}
